<template>
    <div>  
        <div class="loginNav">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">班级荣誉编辑</p>
            <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="save">保存</span>
        </div>
        <div class="content f28 c333" style="padding:0.84rem 0 1rem;">
            <ul class="pl-30">
                <li class="b_line pr-30" v-for="(item,index) in list" :key="index">
                    <img :src="item.checked?checked:notCheck" @click="isChecked(index)"/>
                    <img class="headPic" :src="item.img"/>
                    <span>{{item.name}}</span>
                </li>
            </ul>
        </div>
        <div class="centerBtn f26 c333 text-center pl-30">
            <div>
                <img class="pr-20" :src="checkedAll?checked:notCheck" alt="" @click="all">
                全选
            </div>
            <span class="cfff" @click="delBtn">删除</span>
        </div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            notCheck:require('../../../assets/message/notCheck.png'),
            checked:require('../../../assets/message/checked.png'),
            checkedAll:false,
            list:[
                {
                    checked:true,
                    name:'这是一个文章 标题X1',
                    img:require('../../../assets/message/onShow.png'),
                },{
                    checked:false,
                    name:'这是一个文章 标题X2',
                    img:require('../../../assets/message/onShow.png'),
                },{
                    checked:false,
                    name:'这是一个文章 标题X3',
                    img:require('../../../assets/message/onShow.png'),
                },{
                    checked:false,
                    name:'这是一个文章 标题X4',
                    img:require('../../../assets/message/onShow.png'),
                },{
                    checked:false,
                    name:'这是一个文章 标题X5',
                    img:require('../../../assets/message/onShow.png'),
                },{
                    checked:false,
                    name:'这是一个文章 标题X3',
                    img:require('../../../assets/message/onShow.png'),
                },{
                    checked:false,
                    name:'这是一个文章 标题X4',
                    img:require('../../../assets/message/onShow.png'),
                },{
                    checked:false,
                    name:'这是一个文章 标题X5',
                    img:require('../../../assets/message/onShow.png'),
                },{
                    checked:false,
                    name:'这是一个文章 标题X3',
                    img:require('../../../assets/message/onShow.png'),
                },{
                    checked:false,
                    name:'这是一个文章 标题X4',
                    img:require('../../../assets/message/onShow.png'),
                },{
                    checked:false,
                    name:'这是一个文章 标题X5',
                    img:require('../../../assets/message/onShow.png'),
                },
            ],
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        isChecked(index){
            var list=this.list;
            list[index].checked=!list[index].checked;
            var btn=true;
            for(var i in list){
                if(!list[i].checked){
                    btn=false;
                }
            }
            this.checkedAll=btn;
            this.list=list;
        },
        all(){
            this.checkedAll=!this.checkedAll;
            var list=this.list;
            for(var i in list){
                list[i].checked=this.checkedAll;
            }
            this.list=list;
        },
        delBtn(){
            var list=this.list;
            for(var i=list.length-1 ;i>=0;i--){
                if(list[i].checked){
                    list.splice(i,1);
                }
            }
            if(list.length==0){
                this.checkedAll=false;
            }
            this.list=list;
        },
        save(){
            
        },
        checkedAll(){}
    }
}
</script>

<style scoped>
     /* 修改组件样式 */
     .loginNav{
        position: fixed;
        z-index: 3001;
    }
    .centerBtn{
        height: 0.9rem;
        background: #F3F3F3;
        line-height: 0.9rem;
        position: fixed;
        bottom: 0;
        right:0;
        left:0;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .centerBtn span{
        width:2rem;
        background: #E74444;
        text-align: center;
        display: block;
    }
    .centerBtn img{
        width:.4rem;
        height:.4rem;
    }
    .content{
        line-height: 1.94rem;
    }
    li img:first-child{
        width:.4rem;
        height:.4rem;
        margin-right: .3rem;
    }
    .headPic{
        width:2.4rem;
        height:1.34rem;
        border-radius: .1rem;
        margin-right: .3rem;
        border:1px solid #EBEBEB;
    }
    li{
        display: flex;
        align-items: center;
    }
    .onDuty{
        width:.7rem;
        height:.28rem;
        background:#7C88EE;
        line-height: .28rem;
        display: inline-block;
        text-align: center;
        border-radius: .05rem;
    }
    .content div{
        position: relative;
        padding:.16rem .3rem 0;
        line-height: .34rem;
    }
    .content div img{
        width:.24rem;
        height:.24rem;
        position: absolute;
        left:3rem;
        top:.31rem;
    }
    .content input{
        width:100%;
        height:.54rem;
        background: #F3F3F3;
        border:0;
        border-radius: .1rem;
        text-align: center;
    }
</style>




